.container {
  width: 340px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 1280px) {
  .container {
    width: 1200px;
    padding: 0;
  }
}

.main-body {
  font-family: "Source Sans Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  min-width: 380px;
  color: #3f3f3f;
  margin: 0;
  padding: 0;
}

.main-content {
  background-color: #eaeaea;
}

.main-content__main-block {
  display: flex;
  flex-direction: column;
}

@media (min-width: 1280px) {
  .main-content__main-block {
    flex-direction: row;
    column-gap: 15px;
  }
}
.sorting {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
  padding-bottom: 20px;
}

.sorting__title {
  margin-top: 0;
  margin-bottom: 20px;
}

.sorting__list {
  width: 150px;
  background-color: #fffffb;
  border-radius: 3px;
  border: none;
  opacity: 0.5;
  transition: 0.5s;
}
.sorting__list:hover, .sorting__list:focus {
  opacity: 1;
  transition: 0.5s;
}

@media (min-width: 1280px) {
  .sorting {
    flex-direction: row;
    justify-content: space-between;
  }

  .sorting__title {
    font-size: 28px;
    line-height: 35px;
    margin-bottom: 0;
  }
}
.form {
  background-color: #fffffb;
  padding: 15px;
  border-radius: 4px;
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.04), 0px 6px 10px rgba(0, 0, 0, 0.02);
}

.form__input-zone {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  border: none;
}
.form__input-zone label {
  font-size: 14px;
  position: relative;
}
.form__input-zone label:not(:last-of-type) {
  margin-bottom: 10px;
}
.form__input-zone label:not(.form__product-description)::before {
  content: "";
  position: absolute;
  width: 4px;
  height: 4px;
  background-color: #ff8484;
  border-radius: 50%;
}
.form__input-zone input {
  width: 300px;
  margin-top: 10px;
  padding: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 4px;
}

.form__product-name::before {
  right: 148px;
}

.form__product-description textarea {
  width: 300px;
  height: 60px;
  margin-top: 10px;
  padding: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 4px;
  resize: none;
}

.form__image-link::before {
  right: 83px;
}

.form__final-price::before {
  left: 89px;
}

.form__button-add {
  height: 30px;
  background-color: #7bae73;
  color: #fffffb;
  margin-top: 20px;
  padding: 0;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.5s;
}
.form__button-add:disabled {
  background-color: #e5e5e5;
  color: #b4b4b4;
}

@media (min-width: 1280px) {
  .form {
    width: 270px;
    height: 400px;
    padding: 20px 20px 0 20px;
  }

  .form__input-zone label {
    font-size: 15px;
    width: 270px;
  }
  .form__input-zone label:not(:last-of-type) {
    margin-bottom: 15px;
  }
  .form__input-zone input {
    width: 260px;
    margin-top: 5px;
  }

  .form__product-name::before {
    right: 118px;
  }

  .form__product-description textarea {
    width: 260px;
  }

  .form__image-link::before {
    right: 58px;
  }

  .form__final-price::before {
    left: 83px;
  }

  .form__button-add {
    width: 270px;
    height: 36px;
    margin-top: 24px;
  }
}
.products {
  width: 340px;
  padding-top: 40px;
}

.products__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  margin: 0;
  padding: 0 0 40px 0;
  list-style: none;
}

.products__item {
  position: relative;
  width: 340px;
  background-color: #fffffb;
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.04), 0px 6px 10px rgba(0, 0, 0, 0.02);
  border-radius: 4px;
  cursor: pointer;
  opacity: 0.8;
  transition: 0.5s;
}
.products__item:hover, .products__item:focus {
  opacity: 1;
  transition: 0.5s;
}
.products__item:hover .products__button-clear, .products__item:focus .products__button-clear {
  z-index: 5;
  opacity: 1;
  transition: 0.5s;
}

.products__text-block {
  padding: 15px 10px;
}

.products__product-title {
  margin: 0;
}

.products__product-price {
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
  margin: 0;
}

.products__button-clear {
  position: absolute;
  top: -10px;
  right: -5px;
  width: 32px;
  height: 32px;
  background-color: #ff8484;
  background-image: url("../img/button-clear.svg");
  background-repeat: no-repeat;
  background-position: -4px -2px;
  padding: 0;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  z-index: 0;
  opacity: 0;
  transition: 0.5s;
}

@media (min-width: 1280px) {
  .products {
    padding-top: 0;
    width: min-content;
  }

  .products__list {
    display: grid;
    grid-template-columns: min-content min-content min-content;
    width: auto;
  }

  .products__item {
    width: 282px;
  }
  .products__item img {
    width: 282px;
  }
}